import {v4 as uuidv4} from 'uuid';

export function getTimers() {
  const timers = JSON.parse(localStorage.getItem("timers"));
  if (!timers) {
    return [];
  }
  return timers;
}

export function createTimer(emoji, timespan) {
  setTimers([...getTimers(), {
    id: uuidv4(),
    emoji: emoji,
    startedAt: new Date().getTime(),
    timespan: timespan,
  }]);
}

export function restartTimer(id) {
  setTimers(getTimers().map(timer => {
    if (timer.id === id) {
      return {
        ...timer,
        startedAt: new Date().getTime()
      };
    } else {
      return timer;
    }
  }));
}

export function deleteTimer(id) {
  setTimers(getTimers().filter(timer => timer.id !== id));
}

function setTimers(timers) {
  localStorage.setItem("timers", JSON.stringify(timers));
}
