import React from 'react';
import ReactDOM from 'react-dom/client';
import { createHashRouter, RouterProvider } from 'react-router-dom';
import './index.css';
import reportWebVitals from './reportWebVitals';
import TimerList, { loader as timerListLoader } from './routes/TimerList';
import TimerAdd, { loader as timerAddLoader, action as timerAddAction } from './routes/TimerAdd';
import { action as timerDestroyAction } from './routes/TimerDestroy';
import { action as timerRestartAction } from './routes/TimerRestart';

const router = createHashRouter([
  {
    path: '/',
    element: <TimerList />,
    loader: timerListLoader,
  },
  {
    path: '/add',
    element: <TimerAdd />,
    loader: timerAddLoader,
    action: timerAddAction,
  },
  {
    path: '/timer/:timerID/destroy',
    action: timerDestroyAction,
  },
  {
    path: '/timer/:timerID/restart',
    action: timerRestartAction,
  },
]);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
