import { Form } from 'react-router-dom';
import Progress from './Progress';

export default function Timer({ timer, now }) {
  const elapsed = now.getTime() - timer.startedAt;
  const done = elapsed >= timer.timespan;

  var percent;
  if (done) {
    percent = 0;
  } else {
    percent = 1 - elapsed / timer.timespan;
  }

  var seconds = Math.ceil((timer.timespan - elapsed) / 1000);
  var minutes = Math.floor(seconds / 60);
  var hours = Math.floor(minutes / 60);
  seconds %= 60;
  minutes %= 60;

  var remaining;
  if (hours > 0) {
    remaining = hours.toString() + ":" + minutes.toString().padStart(2, '0') + ":" + seconds.toString().padStart(2, '0');
  } else if (minutes > 0) {
    remaining = minutes + ":" + seconds.toString().padStart(2, '0');
  } else {
    remaining = seconds.toString();
  }

  var display;
  if (done) {
    display = (
      <Form method="post" action={`timer/${timer.id}/restart`}>
        <button className="text-5xl animate-bounce">{timer.emoji}</button>
      </Form>
    );
  } else {
    display = <span className="text-5xl">{remaining}</span>;
  }

  return (
    <div className="flex relative place-content-center m-4 rounded-lg bg-gray-50 p-4">
      { !done && <button className="absolute top-4 left-4 size-10">{timer.emoji}</button> }
      <Progress percent={percent} />
      <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2">
        {display}
      </div>
      <Form method="post" action={`timer/${timer.id}/destroy`}>
        <button type="submit" className="absolute right-4 top-4 size-10 rounded-full bg-gray-200 hover:bg-gray-400"></button>
      </Form>
    </div>
  );
}
