import { useState, useId } from 'react';
import data from '@emoji-mart/data';
import Picker from '@emoji-mart/react';
import { Form, useNavigate, useLoaderData, redirect } from 'react-router-dom';
import { createTimer } from '../timers';

export function loader({request}) {
  const url = new URL(request.url);
  const empty = url.searchParams.get('empty');

  return { empty };
}

export async function action({request, params}) {
  const formData = await request.formData();

  var span = Number(formData.get("hours")) * 60 * 60 * 1000;
  span += Number(formData.get("minutes")) * 60 * 1000;
  span += Number(formData.get("seconds")) * 1000;

  if (span < 1) {
    return null;
  }

  createTimer(formData.get("emoji"), span);

  return redirect('/');
}

export default function TimerAdd() {
  const { empty } = useLoaderData();
  const [emoji, setEmoji] = useState("");
  const [time, setTime] = useState("");
  const [showPlay, setShowPlay] = useState(false);
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const emojiButtonID = useId();
  const navigate = useNavigate();

  function isZero(s) {
    return /^0*$/.test(s);
  }

  function onKeyPress(value) {
    if(time.length >= 6) {
      return;
    }
    if(isZero(value) && isZero(time)) {
      return;
    }

    const t = time.concat(value);

    setTime(t);
    setShowPlay(!isZero(t));
  }

  function onDelete() {
    const t = time.slice(0, -1);

    setTime(t);
    setShowPlay(!isZero(t));
  }

  function toggleEmojiPicker(ev) {
    setShowEmojiPicker(!showEmojiPicker);
  }

  function onEmojiSelect(data) {
    setEmoji(data.native);
    setShowEmojiPicker(false);
  }

  function onClickOutside(e) {
    if (e.target.id === emojiButtonID) {
      return;
    }

    setShowEmojiPicker(false);
  }

  const timePadded = time.padStart(6, '0');
  const hours = timePadded.slice(0, 2);
  const minutes = timePadded.slice(2, 4);
  const seconds = timePadded.slice(4, 6);

  var emojiPicker;
  if (showEmojiPicker) {
    emojiPicker = (
      <div className="absolute">
        <Picker data={data} onEmojiSelect={onEmojiSelect} onClickOutside={onClickOutside} />
      </div>
    );
  }

  var play;
  if (showPlay) {
    play = <button
      type="submit"
      className="col-start-2 text-4xl size-24 rounded-full bg-sky-500 hover:bg-sky-700"></button>;
  }

  var cancel;
  if (!empty) {
    cancel = <button
      onClick={() => navigate(-1)}
      type="button"
      className="col-start-3 text-xl size-20 rounded-full bg-red-200 hover:bg-red-400"></button>;
  }

  return (
    <div className="container mx-auto">
      <Form method="post">
        <input name="emoji" type="hidden" value={emoji} />
        <input name="hours" type="hidden" value={hours} />
        <input name="minutes" type="hidden" value={minutes} />
        <input name="seconds" type="hidden" value={seconds} />

        <div className="flex text-4xl m-4">
          <div className="flex-none p-4">
            <button id={emojiButtonID} onClick={toggleEmojiPicker} type="button">{emoji}</button>
            {emojiPicker}
          </div>
          <div className="flex-auto text-right bg-gray-200 rounded-lg p-4">
            <span className={hours > 0 ? "text-sky-500" : ""}>{hours}h</span>
            <span className={hours > 0 || minutes > 0 ? "text-sky-500" : ""}>{minutes}m</span>
            <span className={hours > 0 || minutes > 0 || seconds > 0 ? "text-sky-500" : ""}>{seconds}s</span>
          </div>
        </div>
        <div className="flex place-content-center">
          <div className="grid grid-cols-3 gap-4 place-items-center place-content-center">
            <Key onClick={(value) => onKeyPress(value)} value="1" />
            <Key onClick={(value) => onKeyPress(value)} value="2" />
            <Key onClick={(value) => onKeyPress(value)} value="3" />
            <Key onClick={(value) => onKeyPress(value)} value="4" />
            <Key onClick={(value) => onKeyPress(value)} value="5" />
            <Key onClick={(value) => onKeyPress(value)} value="6" />
            <Key onClick={(value) => onKeyPress(value)} value="7" />
            <Key onClick={(value) => onKeyPress(value)} value="8" />
            <Key onClick={(value) => onKeyPress(value)} value="9" />
            <Key onClick={(value) => onKeyPress(value)} value="00" />
            <Key onClick={(value) => onKeyPress(value)} value="0" />
            <Key onClick={onDelete} value="" />
            {play}{cancel}
          </div>
        </div>
      </Form>
    </div>
  );
}

function Key({ value, onClick }) {
  return (
    <button onClick={() => onClick(value) } type="button" className="text-4xl size-24 rounded-full bg-gray-200 hover:bg-gray-400">
      {value}
    </button>
  );
}
