import { useLoaderData, Link, redirect } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { getTimers } from '../timers';
import Timer from '../Timer';

export function loader() {
  const timers = getTimers();
  if (timers.length < 1) {
    return redirect('/add?empty=true');
  }
  return { timers };
}

export default function TimerList() {
  const { timers } = useLoaderData();
  const [now, setNow] = useState(new Date());
  const refreshRate = 100;

  useEffect(() => {
    const interval = setInterval(() => setNow(new Date()), refreshRate);
    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <div className="container mx-auto">
      <div>
        {timers.map((timer) =>
          <Timer key={timer.id} timer={timer} now={now} />
        )}
      </div>
      <div className="flex place-content-center">
        <Link to={'add'}>
          <button type="button" className="text-4xl size-24 rounded-full bg-sky-500 hover:bg-sky-700">+</button>
        </Link>
      </div>
    </div>
  );
}
